import React, { useContext, useEffect, useState } from 'react';
import { pegarPreco } from '../../utils/utils';
import { ContextFavoritos } from '../main/ContextFavoritos';
import { Tema } from '../principal/Tema';
import './preco.css'

function Preco({ info, change }) {
    const [preco, setPreco] = useState('')
    const contextoTema = useContext(Tema)
    let letraEscura = ''
    let contextoFavoritos = useContext(ContextFavoritos)

    alterarCor()
    function alterarCor() {
        if (contextoTema.tema) {
            letraEscura = 'veiculos-preco letraEscura'
        } else {
            letraEscura = 'veiculos-preco'
        }
    }

    function click() {
        contextoFavoritos.funcao(preco)
        const star = document.querySelector('.fas')
        star.classList.toggle('active')
    }

    useEffect(() => {
        function alterarPreco(dados) {
            if (JSON.stringify(preco) !== JSON.stringify(dados)) {
                setPreco(dados)
            }
        }
        pegarPreco(info.tipo, info.marca, info.modelo, info.ano, alterarPreco)

    }, [info, preco, change, contextoFavoritos])

    useEffect(() => {
        const listaFavoritos = JSON.parse(localStorage.getItem('favoritos')) || []
        const star = document.querySelector('.fas')
        star.classList.remove('active')
        listaFavoritos.forEach(item => {
            if (JSON.stringify(item) === JSON.stringify(preco)) {
                star.classList.add('active')
            }
        });

    }, [preco])

    return (
        <div className={letraEscura}>
            <h4>Modelo: {preco.Modelo}</h4>
            <h4>Marca: {preco.Marca}</h4>
            <h4>Ano: {preco.AnoModelo}</h4>
            <h4>Combustivel: {preco.Combustivel}</h4>
            <h4>Código Fipe: {preco.CodigoFipe}</h4>
            <h4>Preço Médio: {preco.Valor}</h4>
            <button onClick={click} className="veiculos-button--addLista"><i className="fas fa-star"></i></button>
        </div>

    );
}

export default Preco;