import React, { useContext } from 'react';
import { Tema } from '../principal/Tema';
import './header.css'

function Header() {
    const contextoTema = useContext(Tema)
    let fundo = ''
    alterarCor()
    function alterarCor() {
        if (contextoTema.tema) {
            fundo = 'fundoAzul'
        } else {
            fundo = ''
        }
    }
    function alterarTema(event) {
        contextoTema.tema = event.target.checked
        localStorage.setItem('tema', JSON.stringify(contextoTema.tema))
        contextoTema.setTema(event.target.checked)
    }

    return (
        <header className={fundo}>
            <div className="container cabecalho">
                <h1>Fipe Plus</h1>
                <div className="div-liga--desliga">
                    <h6>Escuro</h6>
                    <input type="checkbox" className="checkbox" id="tema" onChange={alterarTema} checked={contextoTema.tema}/>
                    <label htmlFor="tema" className="botao__liga-desliga"></label>
                    <h6>Claro</h6>
                </div>
            </div>
        </header>
    );
}

export default Header;